/**
 * System imports
 ============================================================================ */
import React from "react";
import { Accordion, Card } from "react-bootstrap";
import AccordionCard from "../../../accordionCard";

/**
   * User imports
   ============================================================================ */
// import SubmitButton from "../../../submitButton";
// import Card from "../../../card";

/**
 * Default export
 */
export default (args) => {
  /**
      * Default Properties
      ============================================================================ */
  let defaults = { namespace: "LeadsLogIn" };

  /**
      * Properties
      ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
      * Barrels
      ============================================================================ */
  const { helpPath3 } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { setHelpPath } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  // const onHelpPath3Click = (link) => {
  //   // console.log(link);
  //   setHelpPath(3, link);
  // };

  /**
      * Return
      ============================================================================ */
  return (
    <>
      <p className="help-title">Mans konts</p>

      <Accordion defaultActiveKey={helpPath3}>
        <AccordionCard
          title="Pārvaldīt iestatījumus"
          eventkey="manage-settings"
          active={helpPath3}
          onClick={() => setHelpPath(3, "manage-settings")}
        >
          <ul className="topic-ul">
            <li>
              <a href="/help/article/how-do-i-view-or-edit-my-account-information">
                Kā apskatīt vai rediģēt sava konta informāciju?
              </a>
            </li>
            <li>
              <a href="/help/article/what-are-the-fees-for-lc-accounts">
                Kādu dokumentāciju es varu sniegt, lai noņemtu konta
                ierobežojumus?
              </a>
            </li>
            <li>
              <a href="/help/article/questions-about-daily-allowance-payments">
                Jautājumi par dienas naudas maksājumiem
              </a>
            </li>
          </ul>
        </AccordionCard>
        <AccordionCard
          title="Konta statuss un verifikācijas"
          eventkey="account-status-and-verification"
          active={helpPath3}
          onClick={() => setHelpPath(3, "account-status-and-verification")}
        >
          <ul className="topic-ul">
            <li>
              <a href="/help/article/how-do-i-view-or-edit-my-account-information">
                How do I view or edit my account information?
              </a>
            </li>
            <li>
              <a href="/help/article/what-are-the-fees-for-lc-accounts">
                What kind of documentation can I provide to remove my account
                limitation?
              </a>
            </li>
            <li>
              <a href="/help/article/questions-about-daily-allowance-payments">
                Questions about daily allowance payments
              </a>
            </li>
          </ul>
        </AccordionCard>
      </Accordion>
    </>
  );
};
